body {
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
  font-size: 16px;
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

a:hover a:focus,
a:visited {
  text-decoration: none;
  outline: none;
}

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato-Regular.eot");
  src: url("../fonts/Lato-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@font-face {
  font-family: 'Roboto Regular';
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@font-face {
  font-family: 'Roboto Conrensed';
  src: url("../fonts/Roboto-Conrensed.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

@-webkit-keyframes load {
  0% {
    width: 0;
  }

  20% {
    width: 0;
  }

  25% {
    width: 10%;
  }

  50% {
    width: 30%;
  }

  75% {
    width: 40%;
  }

  100% {
    width: 100%;
  }
}

@-o-keyframes load {
  0% {
    width: 0;
  }

  20% {
    width: 0;
  }

  25% {
    width: 10%;
  }

  50% {
    width: 30%;
  }

  75% {
    width: 40%;
  }

  100% {
    width: 100%;
  }
}

@keyframes load {
  0% {
    width: 0;
  }

  20% {
    width: 0;
  }

  25% {
    width: 10%;
  }

  50% {
    width: 30%;
  }

  75% {
    width: 40%;
  }

  100% {
    width: 100%;
  }
}

@-webkit-keyframes error {
  0%, 100% {
    left: 0;
  }

  10%, 30%, 50%, 70%, 90% {
    left: -5px;
  }

  20%, 40%, 60%, 80% {
    left: 5px;
  }
}

@-o-keyframes error {
  0%, 100% {
    left: 0;
  }

  10%, 30%, 50%, 70%, 90% {
    left: -5px;
  }

  20%, 40%, 60%, 80% {
    left: 5px;
  }
}

@keyframes error {
  0%, 100% {
    left: 0;
  }

  10%, 30%, 50%, 70%, 90% {
    left: -5px;
  }

  20%, 40%, 60%, 80% {
    left: 5px;
  }
}

.form {
  text-align: left;
}

.form__item {
  margin-bottom: 15px;
}

.form__input {
  font-family: "Roboto Conrensed", sans-serif;
  font-weight: 400;
  width: 100%;
  border: 1px solid #e1e6f0;
  color: #8791a4;
  border-radius: 3px;
  font-size: 0.8em;
  background: #fff;
  padding: 10px 7px 10px 10px;
}

.form__input:focus {
  border: 1px solid #1385c7;
  outline: none;
}

.form__input.ng-invalid.ng-touched {
  border: 1px solid #ff0000;
}

.form__label {
  display: block;
}

.form__title {
  font-size: 0.8em;
  color: #1385c7;
  margin-bottom: 5px;
}

.form__btn-wrap {
  margin-top: 15px;
  display: inline-block;
}

.form__progress-bar {
  position: relative;
  width: 100%;
  height: 0;
  margin-bottom: 7px;
  -webkit-transition: height 0.2s, margin-bottom 0.2s;
  -o-transition: height 0.2s, margin-bottom 0.2s;
  transition: height 0.2s, margin-bottom 0.2s;
  background-color: #1175b0;
}

.form__progress-bar:before {
  content: "";
  position: absolute;
  width: 0;
  height: 100%;
  background-color: #0f6698;
}

.form__progress-bar.active {
  height: 7px;
  margin-bottom: 0;
}

.form__progress-bar.active:before {
  width: 100%;
  -webkit-animation: load 3s forwards;
  -o-animation: load 3s forwards;
  animation: load 3s forwards;
}

.form__btn {
  position: relative;
  padding: 10px;
  overflow: hidden;
  z-index: 2;
  width: 150px;
  height: 45px;
  background-color: #1385c7;
  border: 1px solid #1385c7;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
}

.form__btn span {
  display: inline-block;
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  color: #fff;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.form__btn.error {
  -webkit-animation: error 0.5s forwards;
  -o-animation: error 0.5s forwards;
  animation: error 0.5s forwards;
}

.form__btn:before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  line-height: 40px;
  font-size: 1.3em;
  top: 0;
  left: -100%;
  -webkit-transition: left 0.3s;
  -o-transition: left 0.3s;
  transition: left 0.3s;
}

.form__btn:hover:before {
  left: 0;
}

.form__btn:hover span {
  -webkit-transform: translateX(150px);
  -o-transform: translateX(150px);
  transform: translateX(150px);
}

@-webkit-keyframes popup {
  0% {
    -webkit-transform: rotateX(-60deg);
    transform: rotateX(-60deg);
  }

  60% {
    -webkit-transform: rotateX(20deg);
    transform: rotateX(20deg);
  }

  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}

@-o-keyframes popup {
  0% {
    transform: rotateX(-60deg);
  }

  60% {
    transform: rotateX(20deg);
  }

  100% {
    transform: rotateX(0deg);
  }
}

@keyframes popup {
  0% {
    -webkit-transform: rotateX(-60deg);
    transform: rotateX(-60deg);
  }

  60% {
    -webkit-transform: rotateX(20deg);
    transform: rotateX(20deg);
  }

  100% {
    -webkit-transform: rotateX(0deg);
    transform: rotateX(0deg);
  }
}

.popup {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  -webkit-perspective: 1500px;
  perspective: 1500px;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  transition: opacity 0.1s;
}

.popup.visible {
  visibility: visible;
  opacity: 1;
}

.popup__close {
  cursor: pointer;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50px;
  right: 50px;
  z-index: 100;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.popup__close:hover {
  -webkit-transform: scale(1.2);
  -o-transform: scale(1.2);
  transform: scale(1.2);
}

.popup__close:before {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  content: "";
  width: 100%;
  height: 4px;
  background-color: #f4f5f7;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.popup__close:after {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  content: "";
  width: 100%;
  height: 4px;
  background-color: #f4f5f7;
  -webkit-transform: rotateZ(-45deg);
  transform: rotateZ(-45deg);
}

.popup__inner {
  width: 50%;
  max-width: 400px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  height: auto;
  border: 1px solid #b7c2d9;
  border-radius: 8px;
  -webkit-transform-origin: top center;
  -o-transform-origin: top center;
  transform-origin: top center;
}

.popup__inner.active {
  -webkit-animation: popup 0.5s forwards;
  -o-animation: popup 0.5s forwards;
  animation: popup 0.5s forwards;
}

.popup__header {
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  border-radius: 8px 8px 0 0;
  font-size: 1.2em;
  padding: 20px 50px;
  color: #8791a4;
  background-color: #f4f5f7;
  text-transform: capitalize;
}

.popup__form {
  font-family: "Roboto Regular", sans-serif;
  font-weight: 600;
  padding: 20px 50px;
}